<template>
  <div class="main-container">
    <div class="container">
      <div class="header">
        <div class="header-left">
          <div class="name">
            {{currentUser.name}}
          </div>

        </div>
        <div class="header-right">
          <div>
            <span class="user-loadRate-title">当前负载度</span>
            <el-button type="text"  class="user-loadRate-action" @click="isShowLoadRateDialog=true" v-if="user._id == currentUser._id">编辑</el-button>
            <load-rate :load-rate="currentUser.loadRate" :is-show.sync="isShowLoadRateDialog" :user-id="currentUser._id"></load-rate>
          </div>
          <div class="user-loadRate-value">
            {{currentUser.loadRate||'--'}}%
          </div>
        </div>
      </div>
      <div class="main">
        <div class="main-left">
          <div class="main-left-title">
            个人信息
          </div>
          <div class="user-info">
            <div class="user-info-row">
              <div class="user-info-label">学号/工作证号:</div>
              <div class="user-info-value">{{currentUser.studentID}}</div>
            </div>
            <div class="user-info-row">
              <div class="user-info-label">手机:</div>
              <div class="user-info-value">{{currentUser.phoneNumber}}</div>
            </div>
            <div class="user-info-row">
              <div class="user-info-label">固定电话:</div>
              <div class="user-info-value">{{currentUser.telephone}}</div>
            </div>
            <div class="user-info-row">
              <div class="user-info-label">微信:</div>
              <div class="user-info-value">{{currentUser.wechat}}</div>
            </div>
            <div class="user-info-row">
              <div class="user-info-label">邮箱:</div>
              <div class="user-info-value">{{currentUser.email}}</div>
            </div>
          </div>

        </div>
        <div class="main-right">
          <div class="main-right-container">

            <div class="main-right-title">
              <img  class="icon" src="../assets/icon2.png">
              <div class="work_record">正在参与的项目</div>


            </div>
            <div class="user-project-list">
              <div v-for="(project, index) in userProjectList" >
                {{index === userProjectList.length-1 ? project.project.name : project.project.name + ',' }}
              </div>
            </div>


          </div>

          <div class="main-right-container">
            <div class="main-right-title">
              <img class="icon" src="../assets/icon1.png">
              <div class="work_record">工作记录</div>
            </div>
            <div class="task-container" v-for="(weekTaskList, index) in userTaskList" v-if="index < 4 || isShowAll ">
              <div class="task-time">
                {{handleTime(weekTaskList[0].startTime,'yyyy.MM.dd')}}-{{handleTime(weekTaskList[0].endTime, 'yyyy.MM.dd')}}
              </div>
              <div class="task-content" v-for="(task, index ) in weekTaskList">
                <div class="task-name">
                  {{task.project? task.project.name: ''}}
                </div>
                <div class="task-info">
                  {{task.description ||'--' }}
                </div>
              </div>

            </div>
            <div>
              <el-button type="text" class="task-action" @click="isShowAll=true" v-if="!isShowAll && userTaskList.length>=4">查看全部</el-button>

            </div>



          </div>




        </div>

      </div>
    </div>

  </div>


</template>
<style scoped>
  .header{
    width: 100%;
    display: flex;
    padding: 20px;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.16);
    margin-bottom: 20px;
    flex-wrap: wrap;
    box-sizing: border-box;
    background: white;
  }
  .header-left{
    width: 25%;
  }
  .name{
    font-size: 36px;
    color: #303133;
    text-overflow: ellipsis;
    overflow: hidden
  }
  .user-loadRate-title{
    font-size: 14px;
    color: #606266;
  }
  .user-loadRate-action{
    padding-left: 30px;
    font-size: 14px;

  }
  .user-loadRate-value{
    color:   rgba(0, 0, 0, 0.85);
    font-size: 20px;
  }

  .main{
    width: 100%;
    display: flex;
    padding: 20px;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.16);
    flex-wrap: wrap;
    box-sizing: border-box;
    background: white;
  }
  .main-left{
    width: 25%;
    flex-grow: 0;
    flex-shrink: 0;
  }
  .main-right{
    width: 75%;
    flex-grow: 0;
    flex-shrink: 0;
  }
  .main-left-title{
    color: #303133;
    font-size: 16px;
    margin-bottom: 50px;

  }
  .user-info{
    color:  rgba(0, 0, 0, 0.65);
    font-size: 14px;

  }
  .user-info-row{
    display: flex;
    margin-bottom: 5px;
    line-height: 24px;
  }
  .user-info-label{
    flex-grow: 0;
    flex-shrink: 0;
    padding-right: 15px;
    flex-wrap: nowrap;
    color: #606266;
  }
  .user-info-value{
    /*flex-wrap: wrap;*/
    text-overflow: ellipsis;
    overflow: hidden;
    color: #606266;
  }
  .main-right-container{
    margin-bottom: 15px;
    margin-top: 55px;
  }
  .main-right-container:first-child{
    margin-top: 0;
  }
  .main-right-title{
    display: flex;
    margin-bottom: 20px;
    line-height: 24px;
    align-items: center;
  }
  .icon{
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
  .task-container{
    margin-bottom: 24px;
    color: rgba(0, 0, 0, 0.65);
    font-size: 14px;
  }
  .task-time{
    margin-bottom: 16px;
    color: #909399;
    font-weight: bold;
  }
  .task-name{
    margin-bottom: 7px;
    color: #606266;
    font-weight: bold;

  }
  .task-info{
    color: #606266;
  }

  .task-content{
    margin-top: 25px;
  }
  .task-content:first-child{
    margin-top: 0;
  }

  .user-project-list{
    display: flex;
    flex-wrap: wrap;
    color: #606266;
    font-size: 14px;
    line-height: 20px;
  }
  .user-project-list div{
    margin-right: 10px;

  }
  .task-action{
    font-size: 14px;
  }





  @media (max-width: 500px) {/*宽度不大于1000px时, 隐藏右边侧栏*/
    .header-left{
      width: 100%;
      margin-bottom: 20px;
    }
    .main-left{
      width: 100%;
      margin-bottom: 20px;
    }
    .main-right{
      width: 100%;
    }

  }




</style>

<script>
  import  loadRate from '../components/user/loadRate'
  import {Time} from "../util/time";

  export default {
    data(){
      return {
        userTaskList:[],
        userProjectList:[],
        user:{},
        currentUser:{},
        userId:'',
        isShowAll: false,
        isShowLoadRateDialog: false,
        handleTime: Time.handleTime
      }
    },
    components:{
      loadRate,
    },

    watch:{
      isShowLoadRateDialog(){
        if(!this.isShowLoadRateDialog){
          this.getUserInfo();
        }
      },
      $route: {
        handler(to, from) {
         this.init()
        },
        deep: true
      }
    },
    mounted(){
     this.init()

    },
    methods:{
      init(){
        this.user = this.$user.get();
        if (!this.user) {
          this.$router.push('/login')
        }

        this.userId = this.$route.params.id ?  this.$route.params.id : this.user._id;

        this.getUserInfo()
        this.getUserProjectList();
        //
        this.getUserTaskList();
      },
      getUserInfo(){
        let url = '/users/'+this.userId;

        this.$http.get(url).then(result=>{
          if(result.data.code !==200){
            return //todo
          }

         this.currentUser =  result.data.user;

        })
      },
      getUserProjectList(){
        this.$http.get('/users/'+this.userId+"/projects").then(result=>{
          if(result.data.code !==200){
            return
          }
          let userProjectList = [];
            result.data.projectList.forEach((project)=> {
            if(project&&project.project && project.project.name && project.project.isOnGoing){
             userProjectList.push(project)
            }
          });
          this.userProjectList =  userProjectList
        })

      },
      getUserTaskList(){
        this.$http.get('/users/'+this.userId+"/tasks").then(result=>{
          if(result.data.code !==200){
            return
          }
          this.userTaskList = result.data.taskList

        })
      },
    }

  }
</script>



