<template>
  <el-dialog title="添加成员" :visible.sync="isShow">
    <div>
      <el-table
        :data="userList">
        <el-table-column prop="name" label="姓名">
          <template slot-scope="scope">
            <el-input size="small" placeholder="请输入成员姓名" v-model="scope.row.name"></el-input>
          </template>
        </el-table-column>
        <el-table-column prop="studentID" label="学号/工作证号">
          <template slot-scope="scope">
            <el-input size="small" placeholder="请输入学号/工作证号" v-model="scope.row.studentID"></el-input>
          </template>
        </el-table-column>
        <el-table-column prop="role" label="权限组">
          <template slot-scope="scope">
            <el-select v-model="scope.row.role" placeholder="请选择">
              <el-option
                v-for="item in roleList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
      </el-table>
      <div>
        <el-button type="text" @click="addUser">
          添加
        </el-button>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="isShow = false">取 消</el-button>
      <el-button type="primary" @click="saveUserList">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import {Rule} from '../../util/rule'

  export default {
    props: {
      isShow: {
        type: Boolean,
        default: false
      },
      userList:{
        type:Array,
        default:[]
      },
      data:{
        type:Array,
        default:[]
      }
    },
    mounted(){
      window._changeWorkStatus = (id) => {
        this.changeWorkStatus(id)
      }
    },
    data() {
      return {
        roleList: [{
          value: 'P1',
          label: 'P1'
        }, {
          value: 'P2',
          label: 'P2'
        }, {
          value: 'P3',
          label: 'P3'
        }],
        notifyList:{},
      }
    },
    beforeDestroy(){
      window._changeWorkStatus =  null;
    },
    methods: {
      addUser() {
        this.userList.push({})
      },
      changeWorkStatus(id){
         let info = {
          isWorking:true
        };
        this.$http.put("/users/" + id + "/workingStatue",info).then(result=>{
          this.notifyList[id].close();
          if(result.data.code !==200){
            return //todo
          }
          this.$notify({
            message: '成员入职状态已更改',
            type: 'success'
          });
          this.$emit('changeWorkStatus')
        })
      },
      saveUserList() {
        for (let i = 0; i < this.userList.length; i++) {
          let user = this.userList[i];
          if (!user || !user.name || !user.studentID || !user.role) {
            return this.$message.error('请将用户信息填写完整')
          }
          if (!Rule.user.name(user.name)) {
            return this.$message.error('用户' + user.name + '姓名格式不正确')
          }
          if (!Rule.user.role(user.role)) {
            return this.$message.error('用户' + user.name + '权限格式不正确')
          }
          if (!Rule.user.studentID(user.studentID)) {
            return this.$message.error('用户' + user.name + '学号/工作号格式不正确,字符长度在6~30个字符')
          }
          let existUserStudentID = this.data.find(userInfo=> {return user.studentID == userInfo.studentID});
          if (existUserStudentID) {
            return this.$message.error('姓名'+existUserStudentID.name+'的学号/工作证号已存在在系统中，请修改');
          }
          let existUserName = this.data.find(userInfo=> {return user.name == userInfo.name});
          if (existUserName) {
            return this.$confirm(existUserName.name + '已在系统中存在，是否要修改？', '提示', {
              confirmButtonText: '继续创建',
              cancelButtonText: '修改',
              type: 'warning'
            }).then(() => {
              this.createUserList();
            })
          }
        }
        this.createUserList();


      },
      createUserList(){
        this.$http.post('/users', {userList: this.userList}).then(result => {
          console.log(result)
          result.data.forEach(user=>{
            setTimeout(()=>{
              if(user.code === 200110){
                let notify = this.$notify({
                  type: 'error',
                  dangerouslyUseHTMLString: true,
                  duration: 0,
                  message: user.err + '<br><br><button  onclick="_changeWorkStatus(\''+ user.user._id+'\')" class="el-button el-button--primary el-button--mini ">入职</button>'
                });
                this.notifyList[user.user._id] =  notify;
                return
              }

              if(user.code !==200){
                this.$notify({
                  message: user.err,
                  type: 'error'
                });
              }else{
                this.$notify({
                  message: user.user.name +'添加成功',
                  type: 'success'
                });
                this.isShow = false;
              }
            },10)
          })
        })
      },
    },
    watch: {
      isShow() {
        this.$emit('update:isShow', this.isShow)
      },
      userList: {
        deep: true,
        handler() {
        }
      }
    }
  }

</script>

<style scoped>

</style>



