<template>
  <div class="main-container">
    <div >
      <!--主体上半部分-->
      <div class="nav_project">
        <div class="project_label">
          <div class="project_label_container">{{project.class.name}}</div>
          <div class="project_label_container" v-if="project.label">{{project.label? project.label.name: ''}}</div>
        </div>
        <div class="project_title">
          <div class="project_title_container">{{project.name}}</div>
          <div class="project_introduce">起止时间 : {{project.startTime ? updateShowTime(project.startTime) + ' ~ ' + updateShowTime(project.deadLineTime) : '未设置'}}</div>
          <div class="project_introduce"> {{project.description}}</div>
          <div class="project_handle">
              <div class="project_handle_container" @click="showProjectInfoDialog"
                   v-if="user.projectRole !== 'actor' &&  project.isOnGoing">编辑任务
              </div>
            <el-dropdown @command="openDownNav" class="project_handle_container"
                         v-if="user.projectRole !== 'actor' &&  project.isOnGoing">
              <span class="el-dropdown-link">
                <i class="el-icon-more-outline"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="a">关闭存档</el-dropdown-item>
                <el-dropdown-item command="b" style="color: #F56C6C">删除任务</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
              <!--<div class="project_handle_container" @click="closeFile"-->
                   <!--v-if="user.projectRole !== 'actor' &&  project.isOnGoing">关闭存档-->
              <!--</div>-->
              <!--<div class="project_handle_container" @click="delectProject"-->
                  <!--v-if="user.projectRole !== 'actor' &&  project.isOnGoing">删除任务-->
              <!--</div>-->
          </div>
        </div>
        <div class="project_man">
          <div class="project_man_top">
            <img src="../assets/oderr.png">
            <div class="project_man_top_people">参与人</div>
              <div class="project_man_top_edit" @click="isShowMemberDialog = true"
                   v-if="user.projectRole !== 'actor' &&  project.isOnGoing">编辑
              </div>
          </div>
          <div class="project_men">
              <div v-for="(member, index) in allMemberList" v-bind:class="[(user.projectRole !== 'actor' || user.role !== 'P3')? 'action':'']" v-if="member.user" @click="memberDetails(member)">{{member.user.name}}</div>
          </div>
        </div>
      </div>
      <!--主体下班部分-->
      <div class="main_project">
        <!--&lt;!&ndash;主体下班部分左边&ndash;&gt;-->
        <div class="main_work">
          <div class="main_work_top">
            <img src="../assets/icon1.png">
            <div class="work_record">工作记录</div>
          </div>
          <!--工作记录-->
          <div class="work_details">
            <div class="projectDetails-main">
              <div class="projectDetails-main-content">
                <!--for循环工作记录具体内容-->
                <div class="projectDetails-main-content-logging">
                  <div class="logging-main" v-for="(task, index) in taskList" v-if="index<4 || isShowAll">
                    <div class="work-time">{{handleTime(task[0].startTime, 'yyyy.MM.dd')}}
                      -{{handleTime(task[0].endTime, 'yyyy.MM.dd')}}
                    </div>
                    <div class="work-main" v-for="(item, index) in task">
                      <div class="work-user-name" v-if="item.user">{{item.user.name}}</div>
                      <div class="work-main-content" v-html="item.description||'--'"></div>
                    </div>
                  </div>
                  <div>
                    <el-button type="text" v-if="!isShowAll&& taskList.length>=4" @click="isShowAll=true ">展示全部
                    </el-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!--&lt;!&ndash;主体下班部分右边&ndash;&gt;-->
        <div class="main_progress">
          <div class="main_progress_top">
            <img src="../assets/icon2.png">
            <div class="main_progress_container">进度</div>
              <div class="main_progress_container_edit" @click=" showProgressList"
                   v-if="user.projectRole !== 'actor' &&  project.isOnGoing">编辑
              </div>
          </div>
          <!--进度条总区域-->
          <!--进度条-->
          <div class="progress-main">
            <div class="progress-container" v-for="(progress , index) in project.progressList">
              <div class="progress-bar">
                <div class="progress-line" v-bind:class="[progress.status ? 'active': '']" v-if="index !== 0"></div>
                <div class="progress-ball" v-bind:class="[progress.status ? 'active': '', progress.status === 1 ? 'big-ball': '']"></div>
              </div>
              <div class="progress-title" v-bind:class="[progress.status === 1 ? 'active-text': '']">
                {{progress.name}}
              </div>
            </div>


          </div>

        </div>


      </div>
    </div>

    <project-progress :progress-list="projectProgress" :is-show.sync="isShowProgressList"
                      v-on:sendProgressList="updateProgress"></project-progress>

    <project-member :projectId="projectId" :allUser="userList" :allMember="allMemberList"
                    :is-show.sync="isShowMemberDialog"></project-member>

    <project-info :makeSureText='makeSureText' :classes="classes" :project-info="projectInfo"
                  v-on:sendProjectInfo="updateProject" :is-show.sync="isShowProjectInfoDialog"></project-info>
    <projectSummary :is-show.sync="isShowprojectSummaryDialog" :dialogVisible="isShowprojectSummaryDialog"
                  v-on:sendProjectSummary="sendProjectSummary"></projectSummary>

  </div>
</template>

<script>

  import projectInfo from '../components/project/projectInfo'
  import projectMember from '../components/project/projectMember'
  import projectProgress from '../components/project/projectProgress'
  import projectSummary from '../components/projectSummary'
  import {Time} from "../util/time";


  export default {
    components: {
      projectProgress,
      projectMember,
      projectInfo,
      projectSummary,
    },
    mounted() {
      //获取任务id

      this.projectId = this.$route.params.id;

      this.user = this.$user.get();


      Promise.resolve(this.user.role === 'P1' ? 'actor': this.getUserProjectRole() ).then(role=>{
        if(!role){
          return this.$router.push('/projects')
        }
        this.user.projectRole = role;
        this.getProjectInfo();
        this.getClasses();
        if(role !== 'actor'){
          this.getUserList();
        }

        this.getProjectMember();
        this.getProjectTask()

      });



    },
    data() {
      return {

        projectId: '',//任务列表传过来的数据
        project: {label:{},class:{}},
        allMemberList: [],
        user:{},
        projectInfo: {},
        taskList: [],
        isShowAll: false,
        isShowProgressList: false,
        projectProgress: [],
        isShowMemberDialog: false,
        isShowProjectInfoDialog: false,
        isShowprojectSummaryDialog:false,
        classes: [],
        userList: [],
        value: '',
        makeSureText: '确定',
        handleTime: Time.handleTime
      }
    },
    watch: {
      isShowMemberDialog() {
        if (!this.isShowMemberDialog) {
          this.getProjectMember()
        }
      },
    },
    methods: {

      //二级导航,关闭存档和删除任务
      openDownNav(command){
        console.log(command)
        if(command == 'a'){
          this.isShowprojectSummaryDialog = true
        }else if(command == 'b'){
          this.$confirm('任务将被永久删除，不可恢复。确定删除该任务？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$http.delete('/projects/' + this.projectId).then(res => {
              console.log(res)
              if (res.data.code == 200) {
                this.$message({
                  type: 'success',
                  message: '删除项目成功!'
                });
                this.$router.push('/projects')
              }else {
                this.$message({
                  type: 'err',
                  message: '删除项目失败!'
                });
              }
            })
          });
        }
      },

      sendProjectSummary(data){
        console.log(data)
        this.$http.put('/projects/' + this.projectId + '/close'+'?data='+data).then(result => {
          if (result.data.code !== 200) {
            return
          }
          this.$message({
            type: 'success',
            message: '存档成功!'
          });
          this.getProjectInfo()
        })
      },

      updateShowTime(date){
        var newDate = new Date(date);
        return newDate.getFullYear()+'/'+(newDate.getMonth()+1)+'/'+newDate.getDate();
      },

      getUserProjectRole() {
        return new Promise(resolve => {
          this.$http.get('/projects/' + this.projectId + "/member/" + this.user._id).then(result => {
            if (result.data.code !== 200) {
              return resolve( null)
            }
            resolve( result.data.member.role)
          })

        })
      },

      showProjectInfoDialog() {
        this.isShowProjectInfoDialog = true;
        let projectInfo = {
          name: this.project.name,
          label: this.project.label? this.project.label.name: '',
          class: this.project.class._id,
          description: this.project.description,
          timeArr:[
            this.project.startTime,
            this.project.deadLineTime
          ]
          // deadLineTime:this.project.deadLineTime,
          // startTime:this.project.startTime
        };
        this.projectInfo = projectInfo
      },
      showProgressList() {
        this.isShowProgressList = true;
        let projectProgress = JSON.parse(JSON.stringify(this.project.progressList))
        projectProgress.forEach(progress => {
          if (progress.status === 1) {
            progress.status = true;
          } else {
            progress.status = false;
          }
        });
        this.projectProgress = projectProgress

      },
      getProjectTask() {
        this.$http.get('/projects/' + this.projectId + '/tasks').then(result => {
          if (result.data.code !== 200) {
            return //todo
          }
          result.data.taskList.map((item) => {
            item.map((text) => {
              if(text.description !== ''){
                text.description.replace(/\n/g,"</br>")
                text.description = text.description.replace(/[\n]/g, "<br/>");
                return text.description;
              }
            })

          })
          console.log(result.data.taskList)
          this.taskList = result.data.taskList;
        })
      },
      getUserList() {
        this.$http.get('/users').then((usersInfo) => {
          if (usersInfo.data.code !== 200) {
            return //todo
          }
          this.userList = usersInfo.data.userList
        })
      },
      getClasses() {
        this.$http.get('/classes').then((classesInfo) => {
          if (classesInfo.data.code !== 200) {
            return console.log('err')//todo
          }
          this.classes = classesInfo.data.classList
        });
      },

      //编辑任务节点
      updateProgress(data) {
        this.$http.put('/projects/' + this.projectId + '/progress', {progressList: data}).then((result) => {
          if (result.data.code !== 200) {
            return   this.$message({
              message: result.data.err,
              type: 'error'
            });
          }
          this.getProjectInfo();
          this.isShowProgressList = false;
        })
      },

      //获取任务详情
      getProjectInfo() {
        this.$http.get('/projects/' + this.projectId).then((result) => {
          if (result.data.code !== 200) {
            return
          }
          let project = result.data.project;
          let currentIndex = project.progressList.findIndex(progress => {
            return progress.status;
          });
          project.progressList.forEach((progress, index) => {
            if (index < currentIndex) {
              progress.status = 2;
            } else if (index == currentIndex) {
              progress.status = 1
            } else {
              progress.status = 0
            }
          })
          console.log(project)
          this.project = project
        });

      },
      updateProject(data) {
        console.log(data.timeArr[0])
        console.log(data.timeArr[1])
        let sendProjectInfo = {
          class: data.class,
          description: data.description,
          label: data.label,
          name: data.name,
          startTime:  data.timeArr[0],
          deadLineTime:data.timeArr[1]
        }
        this.$http.put('/projects/' + this.projectId, sendProjectInfo).then(result => {
          if (result.data.code !== 200) {
            return this.$message.error('信息错误')//todo
          }
          console.log(result)
          this.getProjectInfo();
          this.isShowProjectInfoDialog = false
        })
      },

      //获取项目成员
      getProjectMember() {
        this.$http.get('/projects/' + this.projectId + '/member').then(result => {
          if (result.data.code !== 200) {
            return  //todo
          }
          this.allMemberList = result.data.memberList.filter(member=> member.user&&member.user.isWorking)
        });
      },
      memberDetails(member){
        if(this.user.role === 'P3'){
          console.log('你看不了哦')
        }else if (member.user) {
          return this.$router.push('/users/'+member.user._id);
        }
      }
    }
  }
</script>
