<template>
  <div class="main-container">
    <div class="header-action">
      <el-button type="text" @click="showAddUserDialog"> 添加成员 </el-button>
    </div>
    <el-table :data="userList">
      <el-table-column
        prop="name"
        label="姓名"
      >
        <template slot-scope="scope">
          <span>{{scope.row.isWorking ? scope.row.name : scope.row.name + '(已停用)'}}</span>
        </template>
        <!--<span>{{scope.row.isWorking ? '' : '已停用'}}</span>-->
      </el-table-column>
      <el-table-column
        prop="studentID"
        label="学号"
      >
      </el-table-column>
      <el-table-column
        label="初始密码"
        prop="startPassword">
      </el-table-column>
      <el-table-column
        label="权限组"
        prop="role"
        width="100">
        <template slot-scope="scope">
          <el-dropdown @visible-change="getCurrentRowValue(scope.row)" @command="handleCommand">
            <span class="el-dropdown-link">
              {{scope.row.role}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="a"  :disabled="scope.row.isWorking?false:true">删除成员</el-dropdown-item>
              <el-dropdown-item command="b"  :disabled="scope.row.isWorking?false:true">停用帐号</el-dropdown-item>
              <el-dropdown-item command="c"  :disabled="!scope.row.isWorking?false:true">恢复启用</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <add-user-dialog :is-show.sync="isShowAddUserDialog" :userList="sonedUserList" :data="userList" v-on:changeWorkStatus = 'addOutWorkUser'></add-user-dialog>

  </div>
</template>
<style scoped>
  .header-action{
    /*padding: 10px 30px;*/
    text-align: right;
  }
  .el-icon-arrow-down {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-button--text{
    padding-left: 5px;
  }
  .el-button--warning{
    font-size: 12px;
    color: red;
  }
</style>
<script>
  import  addUserDialog from  '../components/user/addUser'
  export default {
    data() {
      return {
        userList:[],
        isShowAddUserDialog: false,
        sonedUserList:[],
        rowData:''
      }
    },
    components:{
      addUserDialog,
    },
    watch:{
      isShowAddUserDialog(){
        if(!this.isShowAddUserDialog){
          this.getUserList();
        }
      },
    },
    mounted() {
      this.user = this.$user.get();
      if (!this.user) {
        return  this.$router.push('/login')
      }
      if(this.user.role === 'P3' || this.user.role === 'P1' ){
        return this.$router.push('/projects')
      }

      this.getUserList()

    },
    methods: {
      getUserList(){
        this.$http.get('/users').then(result=>{
          if(result.data.code !==200){
            return //todo
          }
          console.log(result.data.userList)

          let p1Arr = result.data.userList.filter(item => item.role == 'P1')
          let p2Arr = result.data.userList.filter(item => item.role == 'P2')
          let p3Arr = result.data.userList.filter(item => item.role == 'P3')
          let list = [...p1Arr,...p2Arr,...p3Arr]
          this.userList = list
        })
      },
      addOutWorkUser(){
        this.getUserList()
      },
      showAddUserDialog(){
        this.sonedUserList = []
        this.isShowAddUserDialog = true
      },

      getCurrentRowValue(currentItem){
        console.log(currentItem);
        this.rowData = currentItem;
      },
      handleCommand(command) {
        console.log(command)
        if(command == 'a'){
          let name = this.rowData.name
          this.$confirm('你将彻底删除'+name+'，确定删除？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            let id = this.rowData._id
            this.$http.delete("/users/" + id).then(result=>{
              if(result.data.code !==200){
                return //todo
              }
              this.$notify({
                message: '删除成员成功',
                type: 'success'
              });
              this.getUserList()
            })
          });
        }else if(command == 'b'){
          this.$confirm('停用的成员将无法登入。确定停用？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            console.log(this.rowData)
              let id = this.rowData._id
              let info = {
                isWorking:false
              }
              this.$http.put("/users/" + id + "/workingStatue",info).then(result=>{
                if(result.data.code !==200){
                  return //todo
                }
                this.$notify({
                  message: '停用成员成功',
                  type: 'success'
                });
                this.getUserList()
              })
          });
        }else if(command == 'c'){
          this.$confirm('是否启用该用户', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            console.log(this.rowData)
            let id = this.rowData._id
            let info = {
              isWorking:true
            }
            this.$http.put("/users/" + id + "/workingStatue",info).then(result=>{
              if(result.data.code !==200){
                return //todo
              }
              this.$notify({
                message: '启用成员成功',
                type: 'success'
              });
              this.getUserList()
            })
          });
        }
      }
    }
  }

</script>



