<template>
  <div>
    <el-dialog :visible.sync="isShow">
      <div class="addTask">
        <el-form  label-width="80px" :model="projectInfo">
          <el-form-item label="任务名称" >
            <el-input v-model="projectInfo.name"  placeholder="请输入任务名称"></el-input>
          </el-form-item>
          <el-form-item label="分类" >
            <el-select v-model="projectInfo.class" placeholder="请选择" class="select1" size="small">
              <el-option
                v-for="item in classList"
                :key="item._id"
                :label="item.name"
                :value="item._id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="标签">
            <el-input v-model="projectInfo.label" placeholder="请输入标签"></el-input>
          </el-form-item>
          <el-form-item label="起止时间">
            <el-date-picker
              v-model="projectInfo.timeArr"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="简介">
            <el-input type="textarea" v-model="projectInfo.description" placeholder="请输入简介"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="sendProjectInfo">{{makeSureText}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {

    props: {
      isShow: Boolean,
      projectInfo: {
        type: Object,
        default: function () {
          return {}
        }
      },
      makeSureText:{
        type: String,
        default: '下一步 添加参与人'
      },
      classes:{
        type: Array,
        default: function(){
          return []
        }
      }
    },
    data() {
      return {
        classList: []
      }
    },
    methods:{
      show(){
        this.isShow =  true
      },
      sendProjectInfo(){
        if(!this.projectInfo.name){
          this.$message.error('请输入工程名')
        }else if(this.projectInfo.name.length > 20){
          this.$message.error('任务名称长度超出20字符')
        } else if(!this.projectInfo.class){
          this.$message.error('请选择分类')
        } else{
          this.$emit('sendProjectInfo', this.projectInfo)
        }
      }
    },
    watch:{
      classes:{
        deep:true,
        handler(){
          this.classList = this.classes
        }
      },
      isShow(){
        this.$emit('update:isShow',this.isShow);
      }
    }

  }
</script>



