                  <div class="o-progress-spot" v-bind:class="[project.currentProgressList[index].status? 'action-progress': '']" v-if="index ==0"></div>
                  <div class="o-progress-spot" v-bind:class="[project.currentProgressList[index].status? 'action-progress': '']" v-if="index ==0"></div>
                  <div class="o-progress-line" v-bind:class="[project.currentProgressList[index].status? 'action-progress':'' ]"></div>
                </div>
                <div class="o-progress-row-center">
                  <div class="title" v-bind:class="[project.currentProgressList[index].status === 2 ? 'action-title' : '']">{{progress.name}}</div>
                  <div  v-bind:class="[project.currentProgressList[index].status ? 'action-progress' : '',project.currentProgressList[index].status === 2 ? 'big-dot' : '', 'o-progress-row-dot']"></div>
                </div>
                <div class="o-progress-row-right">
                  <div class="o-progress-line"
                       v-bind:class="[ project.currentProgressList[index+1]&& project.currentProgressList[index+1].status? 'action-progress':'']"></div>
                  <div class="o-progress-spot"
                       v-bind:class="[project.currentProgressList[index+1]&& project.currentProgressList[index+1].status?'action-progress':'']"
                       v-if="index+1 == project.currentProgressList.length"></div>
                  <div class="o-progress-spot"
                       v-bind:class="[project.currentProgressList[index+1]&& project.currentProgressList[index+1].status? 'action-progress':'']"
                       v-if="index+1 == project.currentProgressList.length"></div>
                </div>
              </div>
            </div>
