<script>
  import  projectInfo from '../components/project/projectInfo';
  import  projectMember from '../components/project/projectMember'
  export default {
    components:{
      projectInfo,
      projectMember,
    },
    mounted() {
      this.user = this.$user.get();
      if (!this.user) {
        this.$router.push('/login')
      }
      //获取所有标签
      this.getLabelList();
      this.getClasses();
      if(this.user.role !== 'P3'){
        this.getUserList();
      }
      //获取任务列表
      this.getProjectsList()
    },

    data() {
      return {
        currentClass:'',
        currentLabel:'',
        currentYear:'',
        classes: [],
        labelList: [],
        timeList:[],
        isOnGoing: true,
        projectList:[],
        user:{},
        userList:[],
        memberList:[],
        projectId:'',
        isOnCreateProject: false,
        isOnCreateMember: false,
      }
    },
    computed:{

    },
    watch:{
      isOnCreateMember(){
        if(!this.isOnCreateMember){
          this.getProjectsList()
        }
      }
    },
    methods: {
      //导出数据
      downLoadTable(){
        var url = "?isOnGoing="+ this.isOnGoing;
        if(this.currentLabel){
          url += "&label="+ this.currentLabel;
        }
        if(this.currentClass){
          url += "&class=" + this.currentClass;
        }
        if(this.currentYear){
          console.log(this.currentYear)
          url += "&year=" + this.currentYear;
        }

        console.log(url)
        this.$http({
          url:'/projects/downloadCloseProject'+ url,
          responseType: 'blob',
          method:'get'
        }).then((result)=> { //todo
          const url = window.URL.createObjectURL(new Blob([result.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', '组合结构课题组存档文件' + this.getCurrentDate() + '.xlsx'); //or any other extension
          document.body.appendChild(link);
          link.click();
        });
      },

      getCurrentDate(){
        var newDate = new Date();
        return newDate.getFullYear()+'-'+(newDate.getMonth()+1)+'-'+newDate.getDate();
      },
      updateShowTime(date){
        var newDate = new Date(date);
        return newDate.getFullYear()+'/'+(newDate.getMonth()+1)+'/'+newDate.getDate();
      },
      //添加任务
      showProjectDialog() {
        this.isOnCreateProject = true
      },
      //重置筛选
      resetChoice() {
        this.currentLabel = null;
        this.currentClass =  null;
        this.currentYear = null;
        this.getProjectsList()
      },
      //跳转任务详情
      goToProjectDetail(id) {
          this.$router.push({
            path: '/projects/'+ id,
          })
      },
      //查看存档
      changeIsOnGoing() {
        this.isOnGoing = !this.isOnGoing
        this.getProjectsList()
      },

      getClasses(){
        this.$http.get('/classes').then((classesInfo)=>{
          if(classesInfo.data.code !== 200){
            console.log('err')//todo
          }
          this.classes =  classesInfo.data.classList
        });
      },
      //获取所有标签
      getLabelList() {
        this.$http.get('/labels').then((labelInfo)=>{
          if(labelInfo.data.code !== 200){
            console.log('err')//todo
          }
          this.labelList =  labelInfo.data.labelList
        });
      },

      //获取任务列表
      getProjectsList() {
        var url = "?isOnGoing="+ this.isOnGoing;
        if(this.currentLabel){
          url += "&label="+ this.currentLabel;
        }
        if(this.currentClass){
          url += "&class=" + this.currentClass;
        }
        if(this.currentYear){
          console.log(this.currentYear)
          url += "&year=" + this.currentYear;
        }

        console.log(url)
        this.$http.get('/projects'+ url).then((result)=>{ //todo
          result.data.projectList.map((item) => {
            let progressList = JSON.parse(JSON.stringify(item.progressList))
            let currentIndex = progressList.findIndex((progress) => {
              return progress.status
            });
            let currentProgressList = [];
            progressList.forEach((progress, index) => {
              if (index < currentIndex) {
                progress.status = 1;
              }
              if(index == currentIndex){
                progress.status = 2
              }
            });
            var startIndex = currentIndex - 1 < 0 ? 0 : currentIndex -1;
            var endIndex = startIndex + 2 > progressList.length - 1 ? progressList.length - 1 : startIndex + 2;
            startIndex = endIndex == progressList.length-1 && startIndex>0 && endIndex-startIndex<2? startIndex-1: startIndex;
            endIndex = startIndex ==0 && endIndex<progressList-1? endIndex+1:endIndex;
            for(var i = startIndex; i< endIndex+1; i++){
              currentProgressList.push(progressList[i])
            }
            item.currentProgressList =currentProgressList;

            if (item.progressList.length === 0) {
              item.projressState = true
              item.display = false
            } else {
              item.projressState = false
            }
          });
          console.log(result.data.projectList)

          if (!this.currentLabel && !this.currentClass && !this.currentYear) {
            let list = result.data.projectList.map(item =>{
              if(item.deadLineTime){
                return item.deadLineTime.toString().substring(0,4)
              }
            })
            //数组去重
            let currentList = []; //一个新的临时数组
            for(let i = 0; i < list.length; i++){
              if(currentList.indexOf(list[i]) == -1 && list[i] !== undefined){
                currentList.push(list[i]);
              }
            }
            console.log(currentList)
            this.timeList = currentList
          }
          let haveStartTime = result.data.projectList.filter(item => item.deadLineTime)
          let notHaveStartTime = result.data.projectList.filter(item => !item.deadLineTime)
          haveStartTime.sort((a, b) => a.deadLineTime.localeCompare(b.deadLineTime));
          let dataList = [...haveStartTime,...notHaveStartTime]
          this.projectList = dataList
        });

      },
      getUserList(){
        this.$http.get('/users').then((usersInfo)=>{
          if(usersInfo.data.code !== 200){
            return //todo
          }
          this.userList =  usersInfo.data.userList
        })
      },

      getMember(id){
        return new Promise((resolve)=>{
          this.$http.get('/projects/'+id+'/member').then((result)=>{
            if(result.data.code !==200){
            }
            this.memberList = result.data.memberList;
            resolve()
          });
        })
      },
      //  添加任务
      createProject(projectInfo) {
        console.log(projectInfo)
        let sendProjectInfo = {
          class: projectInfo.class,
          description: projectInfo.description,
          label: projectInfo.label,
          name: projectInfo.name,
          startTime:  projectInfo.timeArr[0],
          deadLineTime:projectInfo.timeArr[1]
        }
        console.log(sendProjectInfo)
       this.$http.post('/projects',sendProjectInfo).then((projectInfo)=>{
         if(projectInfo.data.code !== 200){
           return   this.$message({
             message: projectInfo.data.err,
             type: 'error'
           });
         }
         console.log(projectInfo)
         this.resetChoice()
         this.isOnCreateProject = false;
         this.projectId = projectInfo.data.project._id;
         this.getMember(projectInfo.data.project._id).then(()=>{
            this.isOnCreateMember =  true
         })
       })
      },
    }
  }
</script>





