import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import layout from '../views/layout/layout'
import projectsList from '../views/projectsList'
import project from '../views/project'
import userCenter from '../views/userCenter'
import userList from '../views/userList'
import userTask from '../views/userTask'
import userManager from '../views/userManager'
import userInfo from '../views/userInfo'
import login from '../views/login'





const vueRouter = new Router({
  routes: [
    {
      path: '/login',
      name:'login',
      component: login,
      meta: {
        title: '登录页面'
      }
    },
    {
      path: '/userInfo',
      component: userInfo,
      meta: {
        title: '补全信息'
      }
    },
    {
      path: '/projects',
      name: 'projects',
      component: layout,
      children: [
        {
          path: '',
          component: projectsList,
          meta: {
            title: '任务列表'
          }
        }, {
          path: ':id',
          component: project,
          meta: {
            title: '任务详情'
          }
        }
      ]
    },
    {
      path: '/userCenter',
      name: 'userCenter',
      component: layout,
      children: [
        {
          path: '',
          component: userCenter,
          meta: {
            title: '个人中心'
          }
        },
        {
          path: 'tasks',
          component: userTask,
          meta: {
            title: '任务详情'
          }
        },

      ]
    },
    {
      path: '/users',
      name: 'users',
      component: layout,
      children: [
        {
          path: '',
          component: userList,
          meta: {
            title: '成员列表'
          }
        }, {
          path: 'manager',
          component: userManager,
          meta: {
            title: '管理成员'
          }
        }, {
          path: ':id',
          component: userCenter,
          meta: {
            title: '成员中心'
          }
        }
      ]
    },
    {
      path: '*',
      component: login
    }

  ]
})
vueRouter.beforeEach(function (to, from, next) {
  if(to.meta.title){
    document.title=to.meta.title
  }
  let adminPath = [ '/users'];
  let user = Vue.prototype.$user.get();
  // let selfPath = ['/projects', '/userCenter','userInfo'];
  if (to.path === '/login') {
    return next()
  }
  if(Object.keys(user).length===0){
    return  vueRouter.push('/login');
  }
  if(to.path.match(adminPath)){
    if(user && user.role === "P3"){
      return vueRouter.push('/projects');
    }
  }

  next();
});


export default  vueRouter




