<template>
  <div class="elastic-frame">
    <!--弹框2-->
    <el-dialog :visible.sync="isShow">
      <div class="member-container-title">参与人</div>
      <div class="member-container-content">
        <div v-for="(user, index) in userList" class="member-row">
          <div class="member-name">{{user.name}} {{user.role}}</div>
          <div v-if="user.role === 'P1'" class="">
            <el-button  disabled="disabled" class="member-action" size="small"> 已添加</el-button>
          </div>
          <div v-if="user.role !== 'P1'" class="">
            <el-select v-show="user.projectRole" v-model="user.projectRole" placeholder="请选择" size="small" class="member-action" @change="updateMember(user)">
              <el-option label="参与人" value="actor" v-show="user.onlyOne"></el-option>
              <el-option label="负责人" v-show="user.role ==='P2'" value="admin"></el-option>
              <el-option label="移除任务" value="out" v-show="user.onlyOne" style="color: red;"></el-option>
            </el-select>
            <el-button size="small" v-show="!user.projectRole"  class="member-action"  @click="addMember(user, index)">添加</el-button>
          </div>
        </div>
      </div>
      <div class="member-container-bottom">
        <el-button type="primary" @click="close">完成</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<style scoped>
  .member-container{
    padding: 0 20%;
    box-sizing: border-box;
  }
  .member-container-title{
    margin-bottom: 43px;
    margin-top: -43px;
    font-size: 18px;
  }
  .member-action{
    width: 100px;
  }
  .member-row{
    display: flex;
    align-content: space-around;
    margin-top: 5px;
  }
  .member-name{
    flex-grow: 1;
  }
  .member-container-bottom{
    text-align: center;
  }
  .elastic-frame>>>.el-dialog{
    width: 40%;
  }


</style>
<script>
  export default {
    props: {

      isShow:{
        type:Boolean,
        default: false
      },
      projectId:{
        type: String,
        default: "",
      },
      allUser: {
        type: Array,
        default: function () {
          return []
        }
      },
      allMember: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    data() {
      return {
        dialogFormVisible: true,
        userList:[],
        memberList:[],
      }
    },
    methods: {

      close() {
        this.isShow = false
      },
      addMember(user,index){
        this.$http.post('/projects/'+this.projectId+'/member',{
          user: user._id,
          role: 'actor'
        }).then((result)=>{
          if(result.data.code !==200){
            return this.$message({
              message: "失败",
              type: 'error'
            });
          }
          this.userList[index].projectRole = 'actor';
          this.$message({
            message: '添加成员成功',
            type: 'success'
          });
        })
      },
      updateMember(user){
        console.log(222)
        if(user.projectRole === 'out'){
          this.$http.delete('/projects/'+ this.projectId+'/member/'+user._id).then((result)=>{
            if(result.data.code !== 200){
              return this.$message({
                message: '移除成员失败',
                type: 'error'
              });
            }
            user.projectRole='';
            this.$message({
              message: '更新成功',
              type: 'success'
            });
          })
        }else{
          this.$http.put('/projects/'+ this.projectId+'/member/'+user._id,{role: user.projectRole}).then((result)=>{
            if(result.data.code !== 200){
              return this.$message({
                message: '更新成员权限失败',
                type: 'error'
              });
            }

            this.$message({
              message: '更新成功',
              type: 'success'
            });
          })
        }
        let arr = []
        this.userList.forEach((item) => {
          if(item.projectRole == 'admin'){
            arr.push('1')
          }
        })
        if(arr.length == 1){
          this.userList.forEach((item) => {
            if(item.projectRole == 'admin'){
              item.onlyOne = false
            }
          })
        }else{
          this.userList.forEach((item) => {
              item.onlyOne = true
          })
        }
      },

      handleUserList(){
        let userList = JSON.parse(JSON.stringify(this.userList))
        userList.forEach(user=>{
          user.onlyOne = true
           let member = this.memberList.find(member=>{
             return member.user._id == user._id
           })
          user.projectRole = '';
          if(member){
            user.projectRole = member.role == 'owner'? 'admin': member.role
          }
        })
          //todo sort
        this.userList = userList
        let arr = []
        this.userList.forEach((item) => {
          if(item.projectRole == 'admin'){
            arr.push('1')
          }
        })
        if(arr.length == 1){
          this.userList.forEach((item) => {
            if(item.projectRole == 'admin'){
              item.onlyOne = false
            }
          })
        }
      }
    },
    watch:{
      allUser:{
        deep:  true,
        handler(){
          this.userList = this.allUser;
          this.handleUserList()
        }
      },
      allMember:{
        deep: true,
        handler(){
          this.memberList= this.allMember
          this.handleUserList()
        }
      },
      isShow(){
        this.$emit('update:isShow',this.isShow);
      }
    }

  }
</script>



