<template>
  <el-dialog title="编辑进度节点" :visible.sync="isShow">
    <div>
      <el-table
        :data="progressList"
      >
        <el-table-column
          type="index"
          label="序号"
          width="50">
        </el-table-column>
        <el-table-column
          prop="name"
          label="名称"
        >
          <template slot-scope="scope">
            <el-input size="small" v-model="scope.row.name" placeholder="(输入长度为2~11位)"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="province"
          label="当前进行"
        >
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              active-color="#1890FF"
              inactive-color="#BFBFBF"
              @change="changeStatus(scope)"
            >
            </el-switch>

          </template>

        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="removeProgress(scope.row)">删除</el-button>
          </template>
        </el-table-column>


      </el-table>

      <div class="progress-action">

        <el-button type="text" @click="addProgress">
          添加
        </el-button>

      </div>

    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="isShow = false">取 消</el-button>
      <el-button type="primary" @click="sendProgressList">确 定</el-button>
    </div>
  </el-dialog>
</template>

<style scoped>
  .progress-action {
    padding: 0 60px;
  }

</style>

<script>
  export default {
    props: {
      isShow: Boolean,
      progressList: {
        type: Array,
        default: function (data) {
          return JSON.parse(JSON.stringify(data))
        }
      }
    },
    data() {
      return {
        // isShow: true
        // progressList:[]
      }
    },
    methods: {
      changeStatus(scope) {

        if (scope.row.status) {
          this.progressList.forEach((progress, index) => {
            if (index !== scope.$index) {
              progress.status = false;
            }
          })
        }
      },
      addProgress() {
        this.progressList.push({status: false})
      },
      removeProgress(row) {
        var index = this.progressList.findIndex(progress => {
          return progress._id == row._id
        });
        if (index < 0) return;
        this.progressList.splice(index, 1)
      },
      sendProgressList() {
        let errorProgress = this.progressList.find((item) => {
          return !item.name
        });
        if (errorProgress) {
          return this.$message.error('请输入节点名称')
        }

        this.$emit('sendProgressList', this.progressList);

      }
    },
    watch: {
      isShow() {
        this.$emit('update:isShow', this.isShow)
      },
      progressList: {
        deep: true,
        handler() {
        }
      }
    }
  }

</script>



