<template>
  <div class="container">
    <div class="user-task-title">
      {{handleTime(new Date(getCurrentWeekDay(1)),'yyyy.MM.dd')}}- {{handleTime(new Date(getCurrentWeekDay(6)),'MM.dd')}} 工作记录
    </div>
    <div class="user-loadRate">
      <div class="user-loadRate-title">
        负载率 (根据主观感受自己评估百分比)
      </div>
      <div class="user-loadRate-input">
        <el-input v-model="user.loadRate"  :disabled="hadRecodeWeekTask" size="small"
                  placeholder="(范围为0～100)" type="number" max="100" min="0" @change="numberChange"></el-input>
        %

      </div>
    </div>
    <div class="user-task-container" v-for="(project, index) in currentProjectList">
      <div class="user-task-name">{{project.project.name}}</div>
      <div class="user-task-info">
        <el-input type="textarea" v-model="project.projectTask" :disabled="hadRecodeWeekTask">

        </el-input>
      </div>
    </div>
    <div class="user-task-action">
      <el-button  type="primary" @click="updateUserTask" :disabled="hadRecodeWeekTask">保存</el-button>
    </div>

  </div>

</template>
<style scoped>
  .container{
    padding: 15px 10%;
  }
  .user-task-title{
    padding: 20px 0 10px 0;
    margin-bottom: 60px;
    font-size: 24px;
    color: rgba(0,0,0,0.85);


  }
  .user-loadRate-title{
    padding: 20px 0 10px 0;
    color: rgba(0, 0, 0, 0.65);
    margin-bottom: 10px;

  }
  .user-loadRate {
    /*margin: 10px auto;*/
  }



  .user-loadRate-input {
    width: 200px;
    display: flex;
    align-items: center;
  }

  .user-task-container {
    margin-top: 48px;
  }

  .user-task-name {
    padding: 10px 0;
    color: rgba(0, 0, 0, 0.65);
  }
  .user-task-action{
    text-align: center;
    margin-top: 20px;
  }


</style>
<script>
  import {Time} from "../util/time";

  export default {
    data() {
      return {
        currentProjectList: [],
        user: {},
        hadRecodeWeekTask: true,
        handleTime: Time.handleTime,
        getCurrentWeekDay: Time.getCurrentWeekDay
      }
    },
    mounted() {

      this.init()
    },
    methods: {
      init(){
        this.user = this.$user.get();
        Promise.all([this.getUserProjectList(), this.getCurrentWeekTask()]).then(result => {
          let currentProjectList = [];
          result[0].forEach(project => {
            if (project && project.project && project.project.isOnGoing) {
              currentProjectList.push(project)
            }
          });
          this.hadRecodeWeekTask =  result[1].length>0;
          currentProjectList.forEach((project => {
            let task = result[1].find(task => {
              return task.project._id == project.project._id
            });
            project.projectTask = task ? task.description : '';
          }));
          this.currentProjectList = currentProjectList

        })
      },
      getCurrentWeekTask() {
        return new Promise(resolve => {
          this.$http.get('/users/' + this.user._id + '/currentWeekTask').then((result) => {
            if (result.data.code !== 200) {
              return resolve([])
            }

            resolve(result.data.taskList)
          })
        })
      },
      getUserProjectList() {
        return new Promise(resolve => {
          this.$http.get('/users/' + this.user._id + '/projects').then((result) => {
            if (result.data.code !== 200) {
              return resolve([])
            }
            resolve(result.data.projectList)
          })
        })
      },
      updateUserTask() {
        console.log(this.user.loadRate)
        if (this.user.loadRate< 0 || this.user.loadRate > 100) {
          this.$message.error('负载率格式不正确');
        }else{
          let taskList = this.currentProjectList.map(project => {
            let task = {
              projectId: project.project._id,
              description: project.projectTask
            };

            return task
          });
          let allPromise = taskList.map(task=>{
            return this.updateOneTask(task)
          });
          allPromise.push(this.updateUserLoadRate());
          Promise.all(allPromise).then((result)=>{
            this.$message({
              message: '更新成功',
              type: 'success'
            });
            this.init()
          })
        }
      },
      updateOneTask(task){
        return new Promise(resolve => {
          this.$http.post('/projects/'+task.projectId+'/tasks',{description: task.description}).then((result)=>{
            resolve(result.data)
          })
        })
      },
      updateUserLoadRate(){
        return new Promise(resolve => {
          this.$http.put('/users/'+this.user._id+'/loadRate',{loadRate: this.user.loadRate}).then(result=>{
            if(result.data.code !==200){
              return resolve();
            }
            this.$message({
              message: '更新负载率成功',
              type: 'success'
            });
            this.$user.save(this.user);
            resolve()
          });
        })
      },
      numberChange(){
        let reg = new RegExp("^(((\\d{1,2})[.]((\\d{1,2})?))|100|(?:0|[1-9][0-9]?)|100.00|100.0)$");
        let testNum = this.user.loadRate;
        if(!reg.test(testNum)) {
          this.user.loadRate = 0
        }
      }

    },

  }
</script>



