<template>
  <div :class="[canShow&&hadGoingProject && !hadRecodeWeekTask? 'warn-container': 'notShow-class']">
    <div class="warn" v-if="canShow&&hadGoingProject && !hadRecodeWeekTask">
      请在本周截止日期 ({{handleTime(new Date(getCurrentWeekDay(6)),'MM.dd')}} 24:00) 前提交工作记录。
      <el-button type="text" @click="goToSaveTask">去提交</el-button>
    </div>
    <div class="warn" v-if="canShow&&hadGoingProject && hadRecodeWeekTask">
      本周你已经提交。
      <el-button type="text" class="el-button--text" @click="goToSaveTask">查看</el-button>
    </div>
  </div>
</template>
<style scoped>
  .warn-container{
    position: relative;
    background: wheat;
  }
  .notShow-class{
    margin-top: 10px;
  }
  .warn{
    text-align: center;
    margin-bottom: 15px;
    padding: 10px;
    font-size: 14px;
    color: #606266;
  }
  .el-button--text{
    padding: 0;
  }


</style>
<script>
  import {Time} from "../../../util/time";

  export default {
    data(){
      return {
        endTime:'',
        user:{},
        hadRecodeWeekTask: false,
        hadGoingProject: false,
        canShow: false,
        handleTime: Time.handleTime,
        getCurrentWeekDay: Time.getCurrentWeekDay
      }
    },
    mounted(){

      this.user = this.$user.get();
      this.canShow = !this.$route.path.match('/userCenter/tasks');
      if(this.user.role !== 'P1'){
        Promise.all([this.getUserProjectList(), this.getCurrentWeekTask()]).then(result=>{
          this.hadGoingProject = !!result[0].find(project=> {
            return project&&project.project&& project.project.isOnGoing
          });
          this.hadRecodeWeekTask =  result[1].length >0;

        })
      }

    },
    watch: {
      $route: {
        handler(to, from) {
          if(to.path === '/userCenter/tasks'){
             this.canShow = false
          }else{
            this.canShow = true
          }
        },
        deep: true
      }
      },
    methods:{

      getCurrentWeekTask(){
        return new Promise(resolve => {
          this.$http.get('/users/'+this.user._id+'/currentWeekTask').then((result)=>{
            if(result.data.code !== 200){
              return resolve([])
            }

            resolve( result.data.taskList)
          })
        })
      },
      getUserProjectList(){
        return new Promise(resolve => {
          this.$http.get('/users/'+this.user._id+'/projects').then((result)=>{
            if(result.data.code !== 200){
              return resolve([])
            }
            resolve(result.data.projectList)
          })
        })
      },
      goToSaveTask(){
        this.$router.push('/userCenter/tasks')
      }
    }
  }

</script>



