<template>
  <div>
    <div class="header-main">
      <div class="phone-logo">组合结构课题组</div>
      <el-menu :default-active="activeIndex" class="el-menu-demo header_container" mode="horizontal"
               @select="changePage">
        <div class="logo">组合结构课题组</div>
        <el-menu-item index="/projects">任务</el-menu-item>
        <el-menu-item index="/userCenter">我的主页</el-menu-item>
        <el-menu-item index="/users" v-if="user.role !== 'P3'">成员</el-menu-item>
        <!--<el-menu-item index="4" v-if="user.role !== 'P3'">成员管理</el-menu-item>-->
      </el-menu>

      <div class="header-name">
        <el-dropdown @command="logout">
          <span class="el-dropdown-link">
            {{user.name}}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>登出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>
<style scoped>
  .logo{
    line-height: 61px;
    font-size: 22px;
    margin-right: 20px;
    font-weight: bold;
    color: #1c90f3;
  }
  .phone-logo{
    display: none;
    font-size: 22px;
    line-height: 50px;
    font-weight: bold;
    /*background-color: white;*/
    color: #1c90f3;
    /*padding-left: 10%;*/
    height: 50px;
    border-bottom: red;
    text-align: center;
  }
  .header-main {
    position: relative;
    background-color: rgba(250, 250, 250, 1);
    /*box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.16);*/
  }
  .header-name {
    position: absolute;
    right: 10%;
    top: 18px;
  }
  .header_container {
    display: flex;
    padding: 0 10%;
  }
  @media (max-width: 500px) {/*宽度不大于1000px时, 隐藏右边侧栏*/
    .header-name{
      display: none;
    }
    .header_container {
      padding: 0 21%;
    }
    .logo{
      display: none;
    }
    .phone-logo{
      display: block;
    }
  }
</style>

<script>
  export default {
    data() {
      return {
        activeIndex: '1',
        user: {}
      };
    },
    mounted() {
      this.user = this.$user.get();
      this.setActiveIndex()
    },
    watch: {
      $route: {
        handler(to, from) {
          this.setActiveIndex()
        },
        deep: true
      }
    },
    methods: {
      setActiveIndex(){
        this.activeIndex = "/"+this.$route.path.split('/')[1]
      },
      logout() {
        this.$http.put('/users/logout').then((result) => {
          if (result.data.code !== 200) {
            return
          }
          this.$user.remove();
          this.$router.push({
            path: '/login',
          })
        })
      },
      changePage(key, keyPath) {
        this.$router.push({
          path: key,
        })
      }
    }
  }
</script>



