<template>
    <div>
      <el-dialog
        title="项目总结"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <el-input type="textarea" :rows="2"
          placeholder="请输入项目总结"
          v-model="projectSummary">
        </el-input>
        <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="sendProjectSummary">确 定</el-button>
  </span>
      </el-dialog>
    </div>
</template>

<script>
    export default {
      name: "projectSummary",
      props:{
        dialogVisible:false
      },
      watch:{
        dialogVisible(){
          this.$emit('update:isShow',this.dialogVisible);
          this.projectSummary = ''
        }
      },
      data() {
        return {
          projectSummary:''
        };
      },
      methods: {
        handleClose() {
          this.dialogVisible = false
        },

        //确认
        sendProjectSummary(){
          this.$confirm('关闭存档后,该任务将不再显示在首页,且不能被修改', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            if(this.projectSummary){
              this.$emit('sendProjectSummary', this.projectSummary)
              this.dialogVisible = false
            }else{
              this.$message.error('任务总结不可以是空');
            }

          });
        }
      }
    }
</script>

<style scoped>

</style>



