<template>
  <div>
    <div class="login-container">
      <div class="login-title">登录</div>
      <div class="login-row">
        <el-input placeholder="学号 / 工作证号" v-model="userInfo.studentID"></el-input>
      </div>
      <div class="login-row">
        <el-input placeholder="密码"  v-model="userInfo.password" type="password"></el-input>
      </div>
      <div class="login-row login-action">
        <el-button type="primary" class="big-button" @click="login">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        userInfo: {}
      }
    },
    methods: {
      login() {
        if (!this.userInfo.studentID || !this.userInfo.password) {
          return this.$message.error('学号或密码不能为空');
        }

        this.$http.post('/users/login', this.userInfo).then((result) => {
          let data = result.data;
          console.log(data.code)
          if (data.code !== 200) {
            return this.$message.error('学号或密码错误');
          }
          this.$message({
            message: '登录成功',
            type: 'success'
          });
          this.$user.save(data.user);
          let url = data.user.hadAuthentication ? '/projects' : '/userInfo';
          this.$router.push(url)
        })
      }
    },
  }
</script>

<style scoped>


  .login-container {
    margin: 96px auto 0px auto;
    width: 430px;
    text-align: center;
    border: 1px solid rgba(187, 187, 187, 1);
    padding: 40px;
  }

  .login-title {
    margin: auto;
    color: rgba(16, 16, 16, 1);
    font-size: 20px;
  }

  .login-row {
    margin: 20px 0;
  }

  .login-action {
    margin-top: 50px;
  }

  .big-button {
    width: 100%;
    margin: auto;
  }

  @media (max-width: 500px) {/*宽度不大于1000px时, 隐藏右边侧栏*/
    .login-container {
      margin: 96px auto ;
      width: 100%;
      text-align: center;
      border: 1px solid rgba(187, 187, 187, 1);
      padding: 40px;
      box-sizing: border-box;
    }
  }


</style>



