<template>
  <div class="container">
    <div class="title">欢迎你，{{user.name}}。请补充个人信息</div>
    <el-form :model="userInfo" ref="userInfo" label-width="100px">
      <el-form-item label="设定新密码:" :rules="[
      { required: true }
    ]">
        <el-input placeholder='请输入6-12位字母或数字' v-model="userInfo.password" type="password"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码:" :rules="[
      { required: true }
    ]">
        <el-input placeholder='请输入6-12位字母或数字' v-model="userInfo.rePassword" type="password"></el-input>
      </el-form-item>
      <br>
      <el-form-item label="手机号:">
        <el-input v-model="userInfo.phoneNumber" placeholder="请输入您的手机号"></el-input>
      </el-form-item>
      <el-form-item label="邮箱:">
        <el-input v-model="userInfo.email" placeholder="请输入您的电子邮箱"></el-input>
      </el-form-item>
      <el-form-item label="微信:">
        <el-input v-model="userInfo.wechat" placeholder="请输入您的微信号"></el-input>
      </el-form-item>
      <el-form-item label="固定电话:">
        <el-input v-model="userInfo.telephone" placeholder="只支持数字格式"></el-input>
      </el-form-item>
      <el-form-item label="特长:">
        <el-input v-model="userInfo.skills" placeholder="请输入特长"></el-input>
      </el-form-item>
        <div v-if="errorMsg" class="error">{{errorMsg}}</div>
      <el-form-item>
        <el-button class="big-button" type="primary" @click="save">保存</el-button>
      </el-form-item>
    </el-form>

  </div>

</template>

<script>
  export default {
    mounted() {
      this.user = this.$user.get();
    },
    name: "",
    data() {
      return {
        user: '',
        userInfo: {},
        confirmPassword: '',
        errorMsg: ''
      }
    },
    methods: {
      save() {
        if (!this.userInfo.password) {
          return this.errorMsg = '密码不能为空'
        }
        if (this.userInfo.password !== this.userInfo.rePassword) {
          return this.errorMsg = '密码不一致'
        }
        var regPassword = /^[A-Za-z0-9]{6,12}$/;
        if(!regPassword.test(this.userInfo.password)){
          return  this.$message.error("请输入6~12位的字母或数字");
        }

        if(this.userInfo.phoneNumber){
          var regTelephone = /^[0-9]{11}$/;
          if(!regTelephone.test(this.userInfo.phoneNumber)){
            return  this.$message.error("手机号格式错误");
          }
        }

        if(this.userInfo.email){
          var regEmail = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
          if(!regEmail.test(this.userInfo.email)){
            return  this.$message.error("邮箱格式错误");
          }
        }

        if(this.userInfo.telephone){
          var reg = new RegExp("^[0-9]*$");
          if(!reg.test(this.userInfo.telephone)){
            return  this.$message.error("固定电话只支持数字～");
          }
        }
        this.$http.put('/users/' + this.user._id + '/init', this.userInfo).then((result) => {
          if (result.data.code !== 200) {
            return this.errorMsg = result.data.err;
          }
          this.$user.save(result.data.user);
          this.$router.push('/projects')


        });

      },
    }
  }
</script>

<style scoped>
  .title {
    margin-bottom: 25px;
    text-align: center;
    font-size: 20px;
  }

  .container {
    width: 500px;
    margin: 20px auto;
    border: 1px solid rgb(187, 187, 187);
    padding: 40px 40px 0 40px;
    border-radius: 5px;
    box-sizing: border-box;
  }

  .big-button {
    width: 100%;
  }
  .error{
    color: red;
    text-align: center;
   padding: 10px;
  }

</style>



