<template>
  <div class="main-container text-color">
    <div class="header-action">
      <el-button type="text" @click="goToManagementUser" v-if="user.role === 'P2'"> 成员管理 </el-button>
    </div>
      <el-table :data="userList" @row-click="goToUserInfo" class="user-list-table">
        <el-table-column
          prop="name"
          label="姓名"
          width="80%"
        >
          <template slot-scope="scope">
            <span>{{scope.row.isWorking ? scope.row.name : scope.row.name + '(已停用)'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="role"
          label="权限组"
          width="80%"
        >
        </el-table-column>
        <el-table-column
          label="学生证/工作证号"
          width="130%"
        >
          <template slot-scope="scope">
            <span>{{scope.row.studentID ? scope.row.studentID :'--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="email"
          label="邮箱"
          width="130%"
        >
          <template slot-scope="scope">
            <span>{{scope.row.email ? scope.row.email :'--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="手机号"
          width="130%"
        >
          <template slot-scope="scope">
          <span>{{scope.row.phoneNumber ? scope.row.phoneNumber :'--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="当前参与任务"
        >
          <template slot-scope="scope">
            <!--{{scope.row.projectList}}-->
            <span v-for="project in scope.row.projectList"> {{ project.name}}</span>
            <span v-if="!scope.row.projectList.length">--</span>
          </template>


        </el-table-column>
        <el-table-column
          label="负载率"
          prop="loadRate"
          width="80%"
        >
          <template slot-scope="scope">
          {{scope.row.role == 'P1' ? '' : scope.row.loadRate ? scope.row.loadRate + '%' : '--' + '%'}}
          </template>

        </el-table-column>


      </el-table>
  </div>

</template>
<style scoped>
  .header-action{

    /*padding: 10px 30px;*/
    text-align: right;
  }
  .project-name{
     padding-right: 10px;
    box-sizing: border-box;
  }
  .user-list-table{
    cursor:pointer;
  }
  @media (max-width: 500px) {

  }

</style>

<script>
  export default {
    data() {
      return {
        userList:[],
        user:{}
      }
    },
    mounted() {
      this.user = this.$user.get();
      if (!this.user) {
        return  this.$router.push('/login')
      }
      if(this.user.role === 'P3'){
        return this.$router.push('/projects')
      }

      this.getUserList()

    },
    methods: {
      goToUserInfo(row){
        return this.$router.push('/users/'+row._id);
      },
      getUserList(){
          this.$http.get('/users').then(result=>{
            if(result.data.code !==200){
              return //todo
            }
            console.log(result.data.userList)

            let p1Arr = result.data.userList.filter(item => item.role == 'P1')
            let p2Arr = result.data.userList.filter(item => item.role == 'P2')
            let p3Arr = result.data.userList.filter(item => item.role == 'P3')
            let list = [...p1Arr,...p2Arr,...p3Arr]
            list.map(userInfo=>{
              userInfo.projectList = userInfo.projectList.filter(projectInfo=> projectInfo != null && projectInfo.isOnGoing==true);
              return userInfo;
            });
            this.userList = list
          })
      },
      handleProjectListName(projectList){
        let goingProjectList = []
        projectList.forEach(project=> {
          if(project && project.isOnGoing){
            goingProjectList.push(project.name)
          }
        })
        return goingProjectList
      },
      goToManagementUser(){
        this.$router.push('/users/manager')
      }
    }
  }

</script>保存



