<template>
  <el-dialog title="负载率" :visible.sync="isShow">
    <div>
        <el-input type="number" v-model="loadRate" placeholder="(范围为0～100)"
                  max="100" min="0">
        </el-input>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="saveLoadRate"> 保存 </el-button>
    </div>
  </el-dialog>
</template>
<style scoped>

</style>

<script>
  export  default {
    data(){
      return {}
    },
    props:{
      isShow: Boolean,
      loadRate: Number,
      userId: String
    },
    methods:{
      saveLoadRate(){
        let reg = new RegExp("^(((\\d{1,2})[.]((\\d{1,2})?))|100|(?:0|[1-9][0-9]?)|100.00|100.0)$");
        let testNum = this.loadRate;
        if (this.loadRate< 0 || this.loadRate > 100) {
          this.$message.error('负载率在0~100之间');
        }else if(!reg.test(testNum)){
          this.$message.error("请正确填写负载率")
        }else{
          this.$http.put('/users/'+this.userId+'/loadRate',{loadRate: this.loadRate}).then(result=>{
            if(result.data.code !==200){
              this.$message.error('更新负载率失败')
            }
            this.$message({
              message: '更新负载率成功',
              type: 'success'
            });
            this.isShow = false;

          });
        }
      },
    },
    watch:{
      isShow(){
        this.$emit('update:isShow',this.isShow)
      }
    }
  }
</script>



